const { device, expect, element, by, waitFor } = require('detox');

describe('JoudEnglishApp E2E Tests', () => {
  beforeEach(async () => {
    await device.reloadReactNative();
  });

  it('should show welcome screen on first launch', async () => {
    // Attendre que l'app se charge
    await waitFor(element(by.text('JoudEnglishApp')))
      .toBeVisible()
      .withTimeout(10000);
    
    // Vérifier que l'écran principal est visible
    await expect(element(by.text('JoudEnglishApp'))).toBeVisible();
  });

  it('should navigate to level selection', async () => {
    // Attendre que l'app se charge
    await waitFor(element(by.id('level-selection-button')))
      .toBeVisible()
      .withTimeout(10000);
    
    // Taper sur le bouton de sélection de niveau
    await element(by.id('level-selection-button')).tap();
    
    // Vérifier que l'écran de sélection de niveau est visible
    await waitFor(element(by.text('Choisissez votre niveau')))
      .toBeVisible()
      .withTimeout(5000);
  });

  it('should select A1 level and navigate to dashboard', async () => {
    // Naviguer vers la sélection de niveau
    await element(by.id('level-selection-button')).tap();
    
    // Sélectionner le niveau A1
    await waitFor(element(by.id('level-A1')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('level-A1')).tap();
    
    // Vérifier que le dashboard est visible
    await waitFor(element(by.text('Dashboard')))
      .toBeVisible()
      .withTimeout(5000);
  });

  it('should navigate to exercise selection', async () => {
    // Aller au dashboard
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    
    // Naviguer vers les exercices
    await waitFor(element(by.id('exercises-button')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('exercises-button')).tap();
    
    // Vérifier que l'écran de sélection d'exercices est visible
    await waitFor(element(by.text('Exercices')))
      .toBeVisible()
      .withTimeout(5000);
  });

  it('should start vocabulary exercise', async () => {
    // Naviguer vers les exercices
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    await element(by.id('exercises-button')).tap();
    
    // Sélectionner l'exercice de vocabulaire
    await waitFor(element(by.id('vocabulary-exercise')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('vocabulary-exercise')).tap();
    
    // Vérifier que l'exercice de vocabulaire est lancé
    await waitFor(element(by.text('Vocabulaire')))
      .toBeVisible()
      .withTimeout(5000);
  });

  it('should complete a vocabulary word', async () => {
    // Naviguer vers l'exercice de vocabulaire
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    await element(by.id('exercises-button')).tap();
    await element(by.id('vocabulary-exercise')).tap();
    
    // Attendre que le premier mot soit visible
    await waitFor(element(by.id('vocabulary-word')))
      .toBeVisible()
      .withTimeout(5000);
    
    // Révéler la traduction
    await element(by.id('reveal-translation-button')).tap();
    
    // Vérifier que la traduction est visible
    await waitFor(element(by.id('vocabulary-translation')))
      .toBeVisible()
      .withTimeout(3000);
    
    // Passer au mot suivant
    await element(by.id('next-word-button')).tap();
    
    // Vérifier que le compteur a changé
    await waitFor(element(by.text('2/')))
      .toBeVisible()
      .withTimeout(3000);
  });

  it('should navigate back from exercise', async () => {
    // Naviguer vers l'exercice de vocabulaire
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    await element(by.id('exercises-button')).tap();
    await element(by.id('vocabulary-exercise')).tap();
    
    // Taper sur le bouton retour
    await waitFor(element(by.id('back-button')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('back-button')).tap();
    
    // Vérifier qu'on est revenu à la sélection d'exercices
    await waitFor(element(by.text('Exercices')))
      .toBeVisible()
      .withTimeout(5000);
  });

  it('should test spelling exercise flow', async () => {
    // Naviguer vers l'exercice d'orthographe
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    await element(by.id('exercises-button')).tap();
    
    await waitFor(element(by.id('spelling-exercise')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('spelling-exercise')).tap();
    
    // Vérifier que l'exercice d'orthographe est lancé
    await waitFor(element(by.text('Orthographe')))
      .toBeVisible()
      .withTimeout(5000);
    
    // Vérifier qu'il y a un champ de saisie
    await waitFor(element(by.id('spelling-input')))
      .toBeVisible()
      .withTimeout(3000);
  });

  it('should test reading exercise flow', async () => {
    // Naviguer vers l'exercice de lecture
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    await element(by.id('exercises-button')).tap();
    
    await waitFor(element(by.id('reading-exercise')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('reading-exercise')).tap();
    
    // Vérifier que l'exercice de lecture est lancé
    await waitFor(element(by.text('Lecture')))
      .toBeVisible()
      .withTimeout(5000);
    
    // Vérifier qu'il y a du texte à lire
    await waitFor(element(by.id('reading-text')))
      .toBeVisible()
      .withTimeout(3000);
  });

  it('should test word games exercise flow', async () => {
    // Naviguer vers les jeux de mots
    await element(by.id('level-selection-button')).tap();
    await element(by.id('level-A1')).tap();
    await element(by.id('exercises-button')).tap();
    
    await waitFor(element(by.id('word-games-exercise')))
      .toBeVisible()
      .withTimeout(5000);
    
    await element(by.id('word-games-exercise')).tap();
    
    // Vérifier que les jeux de mots sont lancés
    await waitFor(element(by.text('Jeux de mots')))
      .toBeVisible()
      .withTimeout(5000);
    
    // Vérifier qu'il y a des éléments de jeu
    await waitFor(element(by.id('game-element')))
      .toBeVisible()
      .withTimeout(3000);
  });
});